.padded {
  padding: 2.5vw;
}

.pageTitle {
  font-size: 2vw;
  font-weight: bold;
  margin: 0 0 1vw 0;
  color: rgba(0, 0, 0, 0.75);
}

.friendlyText {
  margin: 0 0 2vw 0;
  color: rgba(0, 0, 0, 0.75);
}

.align-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin: 0 calc(25% - 2.5vw);
  color: rgba(0, 0, 0, 0.75);
  /* text-indent: 2.5vw; */
}

.contentSpanImageHeader {
  width: 100%;
  object-fit: cover;
}

.contentSpanImage {
  width: 100%;
  height: 15vw;
  object-fit: cover;
  object-position: center top;
}

.textSpan {
  color: rgba(0, 0, 0, 0.75);
  padding: 0 0 1vw 0;
}

.navLink-black {
  font-family: "Bebas Neue", cursive;
  color: rgba(0, 0, 0, 0.75);
  text-decoration: none;
  font-weight: normal;
}

.navLink-black:hover {
  color: rgba(0, 0, 0, 1);
}

.navLink-black__selected {
  font-family: "Bebas Neue", cursive;
  color: rgba(0, 0, 0, 1);
  text-decoration: none;
  font-weight: normal;
}

.navLink-white {
  font-family: "Bebas Neue", cursive;
  color: white;
  text-decoration: none;
  font-weight: normal;
}

.navLink-white:hover {
  color: white;
}

.navLink-white__selected {
  font-family: "Bebas Neue", cursive;
  color: white;
  text-decoration: none;
  font-weight: normal;
}

.submitButtonContainer {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin: 0.5vw 0 0 0;
}

.submitButton {
  font-family: "Bebas Neue", cursive;
  font-size: 1.5vw;
  background-color: transparent;
  color: rgba(0, 0, 0, 0.75);
  border: none;
  border-bottom: 1px solid transparent;
  outline: none;
  padding: 0;
}

.submitButton:hover {
  cursor: pointer;
  color: rgba(0, 0, 0, 1);
  border-bottom: 1px solid rgba(0, 0, 0, 1);
  transition: 350ms;
  transition-timing-function: ease-in-out;
}

.linedBreak {
  margin: 5vw 0 0 0;
  border-top: 1px solid rgba(255, 255, 255, 1);
}

@media (orientation: portrait) {
  .padded {
    padding: 7.5vh;
  }
  .align-center {
    margin: 0;
    /* text-indent: 2.5vh; */
  }
  .pageTitle {
    font-size: 3vh;
    margin: 0 0 1vh 0;
  }
  .submitButtonContainer {
    margin: 2vh 0 0 0;
  }
  .submitButton {
    font-size: 2vh;
  }
  .contentSpanImage {
    width: 100%;
    height: 25vw;
    object-fit: cover;
  }
  .linedBreak {
    margin: 8vh 5vh 10.5vh 5vh;
  }
}

@media (orientation: portrait) and (width < 800px) {
  .padded {
    padding: 2.5vh;
  }
  .friendlyText {
    font-size: 1.5vh;
  }
  .align-center {
    font-size: 1.5vh;
  }
  .linedBreak {
    margin: 5vh 0;
  }
}
